<template>
	<div class="container">
		<div class="middle page">
			<div class="side-nav">
				<a href="javascript:void(0)">代孕新闻/News</a>
				<ul>
					<template v-for="(item,$index) in sidebarList">
						<li :class="[item.isActive?'cur':'']" @click="setActive($index)">
							<router-link :to="item.name">{{ item.value }}</router-link>
						</li>
					</template>
				</ul>
				<a href="javascript:void(0)">联系我们/Contact</a>
				<div class="hot-line">
					<h3>健康热线</h3>
					<p>189-07156511</p>
				</div>
			</div>
			<div class="content">
				<div class="title">
					<h3>代孕新闻<span>News</span></h3>
					<div class="locate">
						<span>您现在的位置在 : </span>
						<ul>
							<li>
								<router-link to="index">首页</router-link>
							</li>
							<li>
								代孕新闻
							</li>
						</ul>
					</div>
				</div>
				<div class="news">
					<ul class="list">
						<template v-for="(item,$index) in newsList">
							<li>
								<router-link :to="{name:'details',query:{uid:item.uid}}">
									<h3>{{item.title}}</h3>
									<p>{{item.content}}</p>
									<em>more+</em>
								</router-link>
							</li>
						</template>
					</ul>
					<div class="pagination">
						<span>总数{{allPage*6}}</span>
						<a v-if="curPage != 1" href="javascript:void(0)" @click="pageList(curPage-1)">上一页</a>
						<ul>
							<template v-for="(page,$index) in allPage">
								<li :class="curPage == $index+1 ?'cur':''">
									<a href="javascript:void(0)" @click="pageList($index+1)">{{$index+1}}</a>
								</li>
							</template>
						</ul>
						<a v-if="curPage != allPage" href="javascript:void(0)" @click="pageList(curPage+1)">下一页</a>
						<span>页次{{curPage}}/{{allPage}}</span>
					</div>
				</div>	
			</div>
		</div>
	</div>
</template>
<script>
	import { mapState,mapGetters,mapMutations,mapActions  } from 'vuex'
	export default {
		name: 'container',
		data() {
			return {
				sidebarList: [{
					id: 0,
					value: '代孕知识',
					name: 'details',
					isActive: false
				},{
					id: 1,
					value: '代孕流程',
					name: 'process',
					isActive: false
				},{
					id: 2,
					value: '代孕案例',
					name: 'case',
					isActive: false
				},{
					id: 3,
					value: '代孕套餐',
					name: 'packages',
					isActive: false
				},{
					id: 4,
					value: '代孕新闻',
					name: 'news',
					isActive: true
				}],
				newsList: [],
				allPage: 0,
				curPage: 0
			}
		},
		methods: {
			setActive: function($index){
				var _this = this;
				_this.sidebarList.forEach(function(item,index){
					if( index == $index ){
						_this.sidebarList[index].isActive = true;
					}else{
						_this.sidebarList[index].isActive = false;
					}
				})
			},
			pageList(page){
				var _this = this;
				this.$axios('/api/newslist')
				.then(function (response) {
					var data = response.data;
					_this.newsList = data.list;
					_this.allPage = data.allPage;
					if(page){
						_this.curPage = page;
					}else{
						_this.curPage = data.curPage;
					}
				})
				.catch(function (error) {
				    console.log(error);
			  	});
			}
		},
		mounted: function(){
			this.$store.commit("changeActive",3);
			this.pageList();
		}
	}
</script>


